export const getCorrectPluralFormText = (number: number, text: string): string =>
{
	switch(text){
		case "":{
			if (number >= 10 && number <=20 || (number % 10 == 0) || (number % 10 >= 5))
				return "";
			else if (number % 10 == 1)
				return "";
			else
				return ""
		}

	}
	return text;
}